}

::-webkit-scrollbar-thumb:hover, ::-webkit-scrollbar-thumb:active {
  background: Hsl(var(--g42-border-primary));
}

.g42-btn, .save-workspace .btn {
  padding: var(--spacing-1) var(--spacing-6);
  border: var(--spacing-1) solid Hsl(var(--g42-border-secondary));
  border-radius: var(--border-radius-4);
  color: Hsl(var(--g42-label-secondary));
  font-size: var(--g42-font-size);
  font-family: var(--g42-font-family);
  background-color: transparent;
  outline: none;
  transition-timing-function: cubic-bezier(0.45, 0, 0.15, 1);
  transition-duration: 240ms;
  transition-property: border-color, background-color, color;
}

.g42-btn:hover, .save-workspace .btn:hover {
  border-color: Hsl(var(--g42-border-primary));
  color: Hsl(var(--g42-label-primary));
  background-color: Hsl(var(--g42-bg-03));
}

.g42-btn:focus, .save-workspace .btn:focus, .g42-btn:focus-visible, .save-workspace .btn:focus-visible, .g42-btn:focus-within, .save-workspace .btn:focus-within {
  border-color: Hsl(var(--g42-border-primary));
}

.g42-btn:disabled, .save-workspace .btn:disabled {
