  justify-content: center;
  padding: 0 0 0 var(--spacing-4);
}

.g42-btn-split > span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--spacing-16);
  height: var(--spacing-16);
  margin-left: var(--spacing-4);
