  font-family: "PTSans";
  font-style: normal;
  src: url(data:font/ttf;base64,) format("truetype");
}

@font-face {
  font-weight: 800;
  font-family: "PTSans-Bold";
  font-style: normal;
  src: url(data:font/ttf;base64,) format("truetype");
}

.g42-checkbox, .form-check-input, .g42-radio {
  transition-timing-function: cubic-bezier(0.45, 0, 0.15, 1);
  transition-duration: 240ms;
  transition-property: background-color, border-color, color;
  position: relative;
  width: var(--spacing-14);
  height: var(--spacing-14);
  margin: 0;
  border: var(--spacing-1) solid Hsl(var(--g42-border-primary));
  border-radius: var(--spacing-2);
  background-color: Hsl(var(--g42-bg-01));
  -webkit-appearance: none;
}

.g42-checkbox:disabled, .form-check-input:disabled, .g42-radio:disabled {
  border-color: Hsl(var(--g42-border-secondary));
  background-color: Hsl(var(--g42-border-secondary));
}

.g42-checkbox:focus, .form-check-input:focus, .g42-radio:focus {
  outline: 1px dashed Hsl(var(--g42-label-tertiary));
  outline-offset: 0;
}

.g42-checkbox::after, .form-check-input::after, .g42-radio::after {
  transition-timing-function: cubic-bezier(0.45, 0, 0.15, 1);
  transition-duration: 240ms;
  transition-property: transform, background-color;
  position: absolute;
  top: calc(var(--spacing-1) * -1);
  left: calc(var(--spacing-1) * -1);
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--spacing-14);
  height: var(--spacing-14);
  font-size: var(--spacing-9);
  font-family: "io-icons";
  transform: scale(0);
  content: "\e91b";
}

.g42-checkbox:hover:not(:disabled):not(:checked)::after, .form-check-input:hover:not(:disabled):not(:checked)::after, .g42-radio:hover:not(:disabled):not(:checked)::after {
  color: Hsl(var(--g42-label-tertiary));
  transform: scale(1);
}

.g42-checkbox:checked, .form-check-input:checked, .g42-radio:checked {
  border-color: Hsl(var(--g42-interactive-primary));
  background-color: Hsl(var(--g42-interactive-primary));
}

.g42-checkbox:checked::after, .form-check-input:checked::after, .g42-radio:checked::after {
  color: Hsl(var(--g42-white));
  transform: scale(1);
}

.g42-checkbox:checked:disabled, .form-check-input:checked:disabled, .g42-radio:checked:disabled {
  border-color: Hsl(var(--g42-border-secondary));
  background-color: Hsl(var(--g42-border-secondary));
}

.g42-checkbox:checked:disabled::after, .form-check-input:checked:disabled::after, .g42-radio:checked:disabled::after {
  color: Hsl(var(--g42-label-tertiary));
}

.g42-checkbox + label, .form-check-input + label, .g42-radio + label {
  display: inline-flex;
}

.g42-radio {
  border-radius: 50%;
}

.g42-radio::after {
  top: calc(50% - var(--spacing-10) / 2);
  left: calc(50% - var(--spacing-10) / 2);
  width: var(--spacing-10);
  height: var(--spacing-10);
  border-radius: 50%;
  content: "";
}

.g42-radio:hover:not(:disabled):not(:checked)::after {
