}

.g42-radio:checked {
  border-color: Hsl(var(--g42-interactive-primary));
  background-color: transparent;
}

.g42-radio:checked::after {
  background-color: Hsl(var(--g42-interactive-primary));
}

.g42-radio:checked:disabled::after {
  background-color: Hsl(var(---g42-label-secondary));
}

.g42-input, input[type="text"] {
  padding: var(--spacing-2) var(--spacing-4);
  border: 1px solid Hsl(var(--g42-border-primary));
  border-radius: var(--spacing-4);
  color: Hsl(var(--g42-label-primary));
  font-size: var(--g42-font-size);
  font-family: var(--g42-font-family);
  line-height: calc(var(--spacing-16) + var(--spacing-2));
  background-color: transparent;
  transition-timing-function: cubic-bezier(0.45, 0, 0.15, 1);
  transition-duration: 240ms;
  transition-property: border-color;
}

