  display: none;
}

.g42-input:focus, input:focus[type="text"], .g42-input:focus-visible, input:focus-visible[type="text"], .g42-input:hover, input:hover[type="text"] {
  border-color: Hsl(var(--g42-interactive-primary));
  outline: 0;
}

.g42-input::placeholder, input[type="text"]::placeholder {
  color: Hsl(var(--g42-label-tertiary));
}

.g42-toggle, .switch {
  position: relative;
  display: flex;
  min-height: var(--spacing-16);
  padding: var(--spacing-4) 0 var(--spacing-4) var(--spacing-24);
  border-radius: var(--spacing-8);
  line-height: var(--spacing-16);
  cursor: pointer;
