  border-bottom: var(--spacing-1) solid Hsl(var(--g42-border-primary));
}

.save-workspace .form-check {
  padding: var(--spacing-4) 0;
}

.empty-container-background {
  background-color: Hsl(var(--g42-app-content-bg));
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.add-button {
  background-color: transparent;
  border: 0;
  color: Hsl(var(--g42-label-secondary));
  width: var(--spacing-16);
  height: var(--spacing-16);
  padding: 0;
}

.add-button::before {
  display: flex;
  align-items: center;
  justify-content: center;
  content: "\e90d";
}

.icon-cancel::before {
  content: "\e916";
}

.lock-options-list {
  width: 200px;
}

.channels-link {
  margin-right: var(--spacing-4);
  color: Hsl(var(--g42-label-primary));
  border-radius: var(--border-radius-4);
  overflow: hidden;
  flex-shrink: 0;
}

.link-icon {
  margin-right: 0;
  font-size: var(--spacing-16);
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.25) {
  #root {
    border-right-width: 2px !important;
    border-bottom-width: 2px !important;
    height: 100%;
    width: 100%;
  }
}
